<template>
  <div class="detail">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>

      <h3 class="mb25">角色名称：{{$route.query.name}}</h3>
      <div class="mb20" v-for="item in this.data">
        <div class="separated-text">{{item.name}}</div>
        <el-checkbox :value="true" v-for="(i,k) in item.children" :label="i.name" :key="k">{{i.name}}</el-checkbox>
      </div>
      <div @click="$router.go(-1)" class="btn-clear mt20">返回</div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "角色管理", path: "go(-1)"},{name: "查看权限", path: ""}],
        data: [],
      }
    },
    methods: {
      async getDetail(){
        this.data = await this.$ajax({
          url: '/role/queryRoleElementList.do',
          query:{
            roleId: this.$route.params.id
          }
        })
      }
    },
    mounted(){
      this.getDetail()
    }
  }
</script>
<style lang="sass" scoped>
  .separated-text
    margin-bottom: 5px
    color: $gray-dark

</style>
